import axios from "axios";

export const Finish = ({ title }) => {
  window.onload = () => {
    const ids = localStorage.getItem('favorite');
    axios.post(`${process.env.REACT_APP_SERVER_URL}/regist`, { ids: ids });
    setTimeout(() => {
      window.location.href = '/login';
    }, 3000);
  }

  return (
    <div id="main" className="w-1/3 mx-auto">
      <h1 className="text-3xl font-bold pt-24 pb-16">{title}</h1>
      <h3></h3>
      <p>3</p>
    </div>
  );
};